<template lang="html">
  <div class="">
    Name: {{ profile.name }}
  </div>
</template>

<script>
export default {
  props: {
    id_token: String
  },
  data: function() {
    return {
      profile: {},
      message: '',
      showAlert: false
    }
  },
  mounted() {
    axios
      .get('https://hzyo7y0ukj.execute-api.eu-west-1.amazonaws.com/dev/' + this.profile.principalId, {
        headers: {
          "Authorization": this.id_token
        }
      })
      .then(response => {
        this.profile.name = response.data.name;
        this.profile.email = response.data.email;
        this.profile.image = response.data.image;
        this.profile.principalId = response.data.user_id;
        this.info = null;
      })
      .catch((error) => {
        console.log(error);
        this.message = 'Failed to read data, are you are authorized?';
        this.showAlert = true;
    });
  }
}
</script>

